import axios from "axios";
import React from "react";
import { Link } from "react-router-dom";
import swal from "sweetalert";
import Loading from "./loading";

export default function ListBuku(props) {

    const deleteHandle = async (id) => {
        let url = `https://api-react-2.herokuapp.com/api/perpustakaan/${id}?kode=2402`;
        console.log(id);
        try {
          await axios.delete(url);
          props.getArtikel();
        } catch (err) {
          swal("gagal", "gagal dihapus", "warning");
          console.log(err);
        }
      };
    return(
        <React.Fragment>
            <div className="flex justify-center items-center px-10 my-5">
                <div className="bg-gray-100 shadow-lg w-full px-7 py-3 my-4">
                    <div className="flex">
                        <div className="mt-5">
                            <img className="h-40" src={props.cover} alt="" />
                        </div>
                        <div className="ml-9">
                            <h2 className="text-2xl font-semibold mb-4 capitalize">{props.judul}</h2>
                            <p className="text-xl mb-2 text-gray-500 capitalize">{props.penulis}</p>
                            <p className="text-lg capitalize">{props.penerbit}</p>
                            <p className="text-sm capitalize">tahun {props.tahun}, {props.hal} halaman</p>
                            <div className="flex  py-6">
                            <svg
            onClick={() => {
              swal({
                title: "Are you sure?",
                text: "ingin menghapus file",
                icon: "warning",
                buttons: true,
                dangerMode: true,
              }).then((willDelete) => {
                if (willDelete) {
                  deleteHandle(props.id);
                } else {
                  swal("Your imaginary file is safe!");
                }
              });
            }}
            xmlns="http://www.w3.org/2000/svg"
            className="h-6 w-6 mr-2 hover:text-blue-600"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
            />
          </svg>

                                <Link to={`/update/${props.id}`}>
                                    <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 hover:text-blue-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                                    </svg>
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}