import React from "react";
import axios from "axios";
import swal from "sweetalert";
import { useHistory, useParams } from "react-router";

export default function Update() {
    const [imagePreview, setImagePreview] = React.useState("");
    const [loading, setLoading] = React.useState(false);
    const [values, setValues] = React.useState({
      kode_penulis: "2402",
      judul_buku: "",
      nama_pengarang: "",
      nama_penerbit_buku: "",
      ketebalan_buku: "",
      tahun_terbit_buku: "",
      sinopsis: "",
      cover: "",
    });
    const [error, setError] = React.useState({
      kode_penulis: "2402",
      judul_buku: "",
      nama_pengarang: "",
      nama_penerbit_buku: "",
      ketebalan_buku: "",
      tahun_terbit_buku: "",
      sinopsis: "",
      cover: "",
    });
    let {id} = useParams()
    console.log({id})
    let history = useHistory()

  const getDetail = async () => {
    let url = `https://api-react-2.herokuapp.com/api/perpustakaan/${id}?kode=2402`;
    try {
      let respon = await axios.get(url)
      setValues((values)=>({
          ...values,
          judul_buku:respon.data.data.judul_buku,
          kode_penulis:respon.data.data.kode_penulis,
          ketebalan_buku:respon.data.data.ketebalan_buku,
          cover:respon.data.data.cover,
          nama_penerbit_buku:respon.data.data.nama_penerbit_buku,
          nama_pengarang:respon.data.data.nama_pengarang,
          sinopsis:respon.data.data.sinopsis,
          tahun_terbit_buku:respon.data.data.tahun_terbit_buku,
          
        }))
        setImagePreview(respon.data.data.cover)
    } catch (err) {
        console.log("ada masalah",err)
    }
  };
  const handleChange = (e) => {
    const key = e.target.name;
    const value = e.target.value;
    setValues((values) => ({
      ...values,
      [key]: value,
    }));
    if (key === "judul")
      if (value === "") {
        error.judul = "Judul Tidak Boleh Kosong";
      } else {
        error.judul = "";
      }
    if (key === "artikel")
      if (value === "") {
        error.artikel = "Artikel Tidak Boleh Kosong";
      } else if (value.replace(/\s/g, "").length < 10) {
        error.artikel = "Isi Artikel terlalu Sedikit";
      } else {
        error.artikel = "";
      }
   
  };
  
  const handleUpload = (e) => {
    e.preventDefault();
    let reader = new FileReader();

    setValues((values) => ({
      ...values,
      thumbnail: e.target.files[0],
    }));
    reader.onloadend = () => {
      if (reader.readyState === 2) {
        setImagePreview(reader.result);
      }
    };
    reader.readAsDataURL(e.target.files[0]);
  };
  console.log(values);
  
  const handleSubmit = async (e) => {
    e.preventDefault();
    const url = `https://api-react-2.herokuapp.com/api/perpustakaan/${id}?kode=2402`;
    console.log(url)
    const formData = new FormData();
    formData.append("judul_buku", values.judul_buku);
    formData.append("tahun_terbit_buku", values.tahun_terbit_buku);
    formData.append("sinopsis", values.sinopsis);
    formData.append("kode_penulis", values.kode_penulis);
    formData.append("nama_pengarang", values.nama_pengarang);
    formData.append("nama_penerbit_buku", values.nama_penerbit_buku);
    formData.append("ketebalan_buku", values.ketebalan_buku);
    formData.append("cover", values.cover);
    formData.append("_method", "put")
    setLoading(true);
    try {
      let result = await axios.post(url, formData, {
        headers: {
          "Content-Type": "multipart/form-data ",
        },
      });
      swal("Berhasil", "Data berhasil diupdate", "success");
      console.log(result);
      setLoading(false);
      history.goBack("/");
    } catch (error) {
      swal("Ups", "Terjadi kesalahan saat menyimpan", "error");
      setLoading(false);
      console.log("error");
    }
  };
    
    React.useEffect(()=>{
      getDetail()
    },[])
    return(
    <React.Fragment>
      <div className="h-screen w-full flex justify-center bg-gray-300">
        <div className="w-full md:w-2/5 lg:w-2/5 bg-white overflow-auto">
          <div className="h-40 px-11 py-20 w-full grid grid-cols-1 gap-2 bg-blue-600">
            <div>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-5 w-5 text-white"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fillRule="evenodd"
                  d="M9.707 16.707a1 1 0 01-1.414 0l-6-6a1 1 0 010-1.414l6-6a1 1 0 011.414 1.414L5.414 9H17a1 1 0 110 2H5.414l4.293 4.293a1 1 0 010 1.414z"
                  clipRule="evenodd"
                />
              </svg>
            </div>
            <h1 className="text-3xl capitalize text-center text-white font-semibold relative bottom-10">
              update buku
            </h1>
          </div>
          
          <form
            onSubmit={handleSubmit}
            autoComplete="off"
            action="post"
            className="px-8 py-10 capitalize"
          >
            <div className="grid gap-1 mb-11">
              <label htmlFor="judul">Judul Buku</label>
              <input
                onChange={handleChange}
                value={values.judul_buku}
                name="judul_buku"
                className="border py-2 rounded-lg shadow-md outline-none"
                type="text"
              />
              <p className="text-red-500 capitalize">{error.judul_buku}</p>
            </div>
            <div className="grid gap-1 mb-11">
              <label htmlFor="penulis">penulis</label>
              <input
                onChange={handleChange}
                value={values.nama_pengarang}
                name="nama_pengarang"
                className="border py-2 rounded-lg shadow-md outline-none"
                type="text"
              />
              <p className="text-red-500 capitalize">{error.nama_pengarang}</p>
            </div>
            <div className="grid gap-1 mb-11">
              <label htmlFor="penerbit">penerbit </label>
              <input
                onChange={handleChange}
                value={values.nama_penerbit_buku}
                name="nama_penerbit_buku"
                className="border py-2 rounded-lg shadow-md outline-none"
                type="text"
              />
              <p className="text-red-500 capitalize">
                {error.nama_penerbit_buku}
              </p>
            </div>
            <div className="grid gap-1 mb-11">
              <label htmlFor="tahun terbit">tahun terbit</label>
              <input
                onChange={handleChange}
                value={values.tahun_terbit_buku}
                name="tahun_terbit_buku"
                className="border py-2 rounded-lg shadow-md outline-none"
                type="number"
              />
              <p className="text-red-500 capitalize">
                {error.tahun_terbit_buku}
              </p>
            </div>
            <div className="grid gap-1 mb-11">
              <label htmlFor="banyakHalaman">banyak halaman</label>
              <input
                onChange={handleChange}
                value={values.ketebalan_buku}
                name="ketebalan_buku"
                className="border py-2 rounded-lg shadow-md outline-none"
                type="number"
              />
              <p className="text-red-500 capitalize">{error.ketebalan_buku}</p>
            </div>
            <div className="grid gap-1 mb-11">
              <label htmlFor="sinopsis">sinopsis</label>
              <textarea
                onChange={handleChange}
                value={values.sinopsis}
                className="outline-none rounded-md border shadow-md"
                name="sinopsis"
                id=""
                cols="30"
                rows="10"
              ></textarea>
              <p className="text-red-500 capitalize">{error.sinopsis}</p>
            </div>
            <div className="grid gap-1 mb-11">
              <label htmlFor="cover">cover buku</label>
              <input
                onChange={handleUpload}
                name="cover"
                className="border py-2 rounded-lg shadow-md outline-none"
                type="file"
              />
              <img className="h-72 my-6" src={imagePreview} alt="" />
            </div>
            <div>
              <button
                type="submit"
                style={{ width: "100%" }}
                className="capitalize py-2 text-white bg-blue-600"
              >
                {loading? "loading...":"upload"}
              </button>
            </div>
          </form>
        </div>
      </div>
        </React.Fragment>
    )
}