import React from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import Home from './component/home';
import TambahBuku from './component/tambahBuku';
import Update from './page/update';
function App(){
  return (
    <BrowserRouter>
      <Switch>
        <Route path="/" exact><Home></Home></Route>
        <Route path="/update/:id"><Update></Update></Route>
        <Route path="/tambah-buku/"><TambahBuku></TambahBuku></Route>
      </Switch>
    </BrowserRouter>

  )
}

export default App;