import { NavLink } from "react-router-dom";
import ListBuku from "./listBuku";
import Search from "./search";
import axios from "axios";
import React from "react";
import Loading from "./loading";

export default function Home(){
    const [artikel, setArtikel] = React.useState([])
    const [loading, setLoading] = React.useState(false)
    const getArtikel = async()=>{
        setLoading(true)
        let url = `https://api-react-2.herokuapp.com/api/perpustakaan?keyword=&per_page=100&page_size&kode=2402`
        try{
            let respon = await axios.get(url)
            console.log(respon.data.data.data)
            setArtikel(respon.data.data.data)
            setLoading(false)
        }catch(err){
            console.log(err)
            setLoading(false)
        }
    }

    React.useEffect(()=>{
        getArtikel()
    },[])
    return(
        <div className="h-screen w-full flex justify-center overflow-auto bg-gray-300">
            <div className="w-full md:w-2/5 lg:w-2/5 bg-white overflow-auto">
                <Search></Search>
                <h1 className="font-semibold text-3xl ml-11 mt-8">List Buku</h1>
                {loading? (
                    <React.Fragment>
                        <Loading></Loading>
                        <Loading></Loading>
                        <Loading></Loading>
                        <Loading></Loading>
                        <Loading></Loading>
                        <Loading></Loading>
                        <Loading></Loading>
                    </React.Fragment>
                ):(
                    artikel?.map((i, key)=>(
                        <ListBuku
                        key={key}
                        id={i.id}
                        judul={i.judul_buku}
                        penulis={i.nama_pengarang}
                        penerbit={i.nama_penerbit_buku}
                        tahun={i.tahun_terbit_buku}
                        hal={i.ketebalan_buku}
                        cover={i.cover}
                        getArtikel={getArtikel}
                        ></ListBuku>
                    ))
                )}
                <div style={{bottom:"10%"}} className="fixed px-7">
                    <NavLink to={`/tambah-buku`} className="text-center ri pt-2 fixed rounded-full h-14 w-14 text-white text-3xl bg-blue-600 capitalize ">+</NavLink>
                </div>
            </div>
        </div>
    )
}